import React, { Component } from 'react';




export default class Project extends Component {
    state = {
        showInfo: false
    } 
    handleInfo = () => {
        this.setState({
            showInfo:!this.state.showInfo
        })
    }

   handleExit = () => {
       this.setState({
           showInfo:!this.state.showInfo
       })
   }


    render() {

     let {name, languagesIcons, source, info, preview, picture} = this.props.item;






        return (
           <div className="project">
               <div className="icons">
                  
                   {languagesIcons.map(icon =>
                    <i className={icon} key={icon}></i>
                    )}
               </div>
               <h3>{name}</h3>
               <img src={ process.env.PUBLIC_URL + picture} alt='' onClick={this.handleInfo} />
               <span className="infos" onClick={this.handleInfo}>
               <i className="fas fa-plus-circle">Preview item</i>
               </span>

               {
                   this.state.showInfo && (
                       <div className="showInfos">
                           <div className="infosContent">
                               <div className="head">
                               <a href={preview} rel="noopener noreferrer"
                                       className="button-preview" target="_blank">Preview</a>
                                   <div className="sourceCode">
                                       <a href={source} rel="noopener noreferrer"
                                       className="button" target="_blank">Source code</a>
                                       </div>
                               </div>
                         <p className="text">{info}</p>

                        <div className="button return" onClick={this.handleInfo}>Back</div>      
                       </div>

                       </div>


                   )
               }
                   

           
       </div> )
    }
}
