import React from 'react';
import Navigation from '../components/Navigation';
import { CopyToClipboard } from 'react-copy-to-clipboard';





const Contact = () => {
    return (
        <div className='contact'>
            <Navigation />
        <div className="contactContent">
           
         <div className="header"></div>
         

         <div className="contactBox">

          <h1>Contactez-moi</h1>
          <ul>
              <li>
                  <i className="fas fa-map-marker-alt"></i>
                  <span>Tunis</span>
              </li>
              <li>
                  <i className="fas fa-mobile-alt"></i>
                  <CopyToClipboard text="+21697442612">
                      <span className="clickInput" onClick={() => { alert('Téléphone copié!');}}>+21697442612</span>
                  </CopyToClipboard>
                  
              </li>
              
              <li>
                  <i className="fas fa-envelope"></i>
                  <CopyToClipboard text="aniskhemiri6@gmail.com">
                      <span className="clickInput" onClick={() => { alert('Téléphone copié!');}}>aniskhemiri6@gmail.com</span>
                  </CopyToClipboard>
                  
              </li>
          </ul>
        
          <div className="socialNetwork">
              <ul>
                  <a href="https://www.linkedin.com/in/anis-khemiri-5b4062218/" target="_blank"
                  rel="noopener noreferrer">
                      <h4>LinkedIn</h4>
                      <i className="fab fa-linkedin"></i>
                  </a>
                  <a href="https://github.com/Anis-khemiri" target="_blank"
                  rel="noopener noreferrer">
                      <h4>Github</h4>
                      <i className="fab fa-github"></i>
                  </a>
                  <a href="http://www.google.com" target="_blank"
                  rel="noopener noreferrer">
                      <h4>Twitter</h4>
                      <i className="fab fa-twitter"></i>
                  </a>
                  <a href="https://codepen.io/your-work?item_type=collection" target="_blank"
                  rel="noopener noreferrer">
                      <h4>CodePen</h4>
                      <i className="fab fa-codepen"></i>
                  </a>

              </ul>
          </div>
         
          </div>
         
        </div>
        </div>
    );
};

export default Contact;