import React from 'react';
import { NavLink } from 'react-router-dom';
import anis1 from './../media/anis1.jpg'

const Navigation = () => {
    return (
        <div className="sidebar">
         <div className="id">
             <div className="idContent">
              <img src={anis1} alt="profil-pic"/>
              <h3>Khemiri Anis</h3>
            
                   
             </div>

         </div>
         
        <div className="navigation">
          <ul>
              <li>
              <NavLink exact to="/" activeClassName="navActive">
               <i className="fas fa-home"></i>
               <span>Home</span>

              </NavLink>
              </li>
              <li>
              <NavLink exact to="/knowledges" 
              activeClassName="navActive">
               <i className="fas fa-mountain"></i>
               <span>Skills</span>

              </NavLink>
              </li>
              <li>
              <NavLink exact to="/portfolio" activeClassName="navActive">
               <i className="fas fa-images"></i>
               <span>Portfolio</span>

              </NavLink>
              </li>
              <li>
              <NavLink exact to="/contact" activeClassName="navActive">
               <i className="fas fa-address-book"></i>
               <span>Contact</span>

              </NavLink>
              </li>
          </ul>

        </div>

    
    <div className="socialNetwork">

          <ul>
              <li>
                  <a href="https://www.linkedin.com/in/anis-khemiri-5b4062218/" target="_blank"
                  rel="noopener noreferrer"><i className="fab fa-linkedin"></i></a>
              </li>
              <li>
                  <a href="https://github.com/Anis-khemiri" target="_blank"
                  rel="noopener noreferrer"><i className="fab fa-github"></i></a>
              </li>
              <li>
                  <a href="https://www.google.com" target="_blank"
                  rel="noopener noreferrer"><i className="fab fa-twitter"></i></a>
              </li>
              <li>
                  <a href="https://codepen.io/your-work?item_type=collection" target="_blank"
                  rel="noopener noreferrer"><i className="fab fa-codepen"></i></a>
              </li>
          </ul>
          <div className="signature">

            <p>by Anis Khemiri</p>
          </div>

       </div>




       </div>



       


    );
};

export default Navigation;